  <div class="download flex_c">
    <div class="download_icon flex_c">
      <img
        src="https://kshop-pro-data.oss-cn-hangzhou.aliyuncs.com/kshop-pro-data_35b194a4a7844cdfb889593c9f9a1f03.png"
        alt
        class="download_logo"
      />
      <img
        src="https://kshop-pro-data.oss-cn-hangzhou.aliyuncs.com/kshop-pro-data_db076b177c10467f9f53e09668f070ce.png"
        alt
        class="download_title"
      />
    </div>
    <div class="download_code flex_r">
      <!-- ios版 -->
      <div class="download_code_main flex_c" v-if="is_ios_android">
        <div class="download_code_iosBox flex_c">
          <img
            src="http://kshop-pro-data.oss-cn-hangzhou.aliyuncs.com/kmh/AppPackage/ios.png"
            alt
            class="download_code_qrCode"
          />
          <div class="download_code_tip flex_r">
            <span class="iosLogo_download_ios">手机扫码下载</span>
          </div>
        </div>
        <a
          :class="[{'download_btn_vlid':is_showTip},'download_code_iosbtn']"
          @click="downLoadIos"
          href="https://itunes.apple.com/cn/app/id1487760048?mt=8"
        >下载安装</a>
      </div>

      <!-- Android版 -->
      <div class="download_code_main flex_c" v-if="!is_ios_android">
        <div class="download_code_androidBox flex_c">
          <img
            src="http://kshop-pro-data.oss-cn-hangzhou.aliyuncs.com/kmh/AppPackage/android.png"
            alt
            class="download_code_qrCode"
          />
          <div class="download_code_tip flex_r">
            <!-- <img
              src="https://kshop-pro-data.oss-cn-hangzhou.aliyuncs.com/kmh/AppPackage/yun_download_page/android_icon.png"
              alt
              class="iosLogo"
            /> -->
            <span class="iosLogo_download_android">手机扫码下载</span>
          </div>
        </div>
        <a
          :class="[{'download_btn_vlid':is_showTip},'download_code_iosbtn']"
          @click="downLoadAndroid"
          href="http://ba-apk.oss-cn-shanghai.aliyuncs.com/apk/%E9%B2%B2%E7%BE%8E%E8%81%94%E7%9B%9F1.2.apk"
        >下载安装</a>
      </div>
    </div>
    <!-- <div v-if="is_showTip" class="tip">微信用户请点击微信右上角选择"在浏览器中打开"</div> -->
