    <div class="downloadn_tip" v-if="is_showTip">点击微信右上角"..."<br>然后选择"在浏览器中打开"</div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      is_showTip:false,
      is_ios_android:false
    };
  },
  methods: {
    downLoadIos() {
      console.log("开始下载IOS版");
    },
    downLoadAndroid() {
      console.log("开始下载Android版");
    },
    is_weixin() {
            var ua = navigator.userAgent.toLowerCase();var u = navigator.userAgent, app = navigator.appVersion;
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
            var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
            console.log(ua)
            if (isIOS) {
              // console.log('苹果手机')
                this.is_ios_android = true
            }
            else{
              this.is_ios_android = false
            }
            if (ua.match(/MicroMessenger/i) == "micromessenger") {
                // alert("是")
                this.is_showTip = true;
            } else {
              // alert("否")
                this.is_showTip = false;
            }
        }
  },
  mounted(){
    this.is_weixin();
  }
};
</script>

