  background: url("https://kshop-pro-data.oss-cn-hangzhou.aliyuncs.com/kshop-pro-data_29c3a5c3dca3433eaf4e010fc48b5544.png")
    no-repeat;
  background-size: 750px auto;
}
.download_icon {
  align-items: center;
  margin-top: 182px;
}
.download_logo {
